@use "global" as *;

.place__inner {
    // background: #F0EDDE;
    background: #F9F8F2;
    padding-top: rem(60);
    padding-bottom: rem(38);
    padding-left: rem(30);
    padding-right: rem(30);
    margin: 0 calc(50% - 50vw);
	width: 100vw;
    overflow-x: hidden;
    margin-top: rem(50);

    @include mq('md') {
        margin-top: rem(100);
    }

    @include mq('lg') {
        width: rem(1226);
        max-width: 100%;
        margin-inline: auto;
        margin-top: rem(125);
        display: flex;
        // align-items: center;
        justify-content: center;
        padding-top: rem(31);
        padding-bottom: rem(37);
        padding-left: 0;
        padding-right: 0;
    }
}

.place__title {
    width: rem(400);
    max-width: 100%;
    margin-inline: auto;
    @include mq('md') {
        width: 80%;
    }
    @include mq('lg') {
        // width: rem(574);
        width: 44.290375%;
        margin: 0;
        margin-top: rem(121);
    }
}

.place__subtitle {
    // font-family: $second-font-family;
    // font-size: rem(22);
    // font-weight: 500;
    padding-left: rem(50);
    @include mq('lg') {
        padding-left: 0;
    }
}

.place__subtitle.sub__title {
    &::before {
        display: none;
    }

}

.place__text {
    font-size: max(rem(16), 14px);
    font-weight: 400;
    line-height: 1.625;
    padding-top: rem(29);
    padding-bottom: rem(18);
    @include mq('lg') {
        font-size: rem(18);
        position: relative;
        padding-bottom: rem(30);
        // margin-left: rem(87);
            &::after {
            content: '';
            position: absolute;
            width: rem(574);
            height: rem(2);
            background: $accent-color;
            bottom: 0;
            left: rem(-145);
        }
    }
    @include mq('xl') {
        margin-left: rem(87);
    }
 
}

.place__img {
    border-top: 2px solid $accent-color;
    padding-top: rem(39);
    margin-left: rem(-31);
    margin-right: rem(-24);
    @include mq('lg') {
        padding-top: 0;
        border-top: 0 solid #000;
        margin-left: rem(48);
        margin-top: rem(15);
        // grid-row: 1;
        // grid-column: 2;
    }
}

.place__img img {
    padding-left: rem(30);
    padding-right: rem(30);
    // width: 600px;
    // max-width: 100%;
    margin-inline: auto;
    @include mq('md') {
        width: 500px;
    }
    @include mq('lg') {
        width: rem(524);
        height: rem(514);
        padding-right: 0;
        padding-left: 0;
    }
}