@use "global" as *;

.section-title {
    text-align: center;
    line-height: 1;
}

.section-title h2 {
    font-size: max(rem(24), 22px);
    font-weight: 400;
    font-family: $second-font-family;
    position: relative;
    padding-bottom: rem(36);
    letter-spacing: 0.05em;
    line-height: 1.66666666;

    @include mq('md') {
        padding-bottom: rem(58);
        font-size: rem(38);
        line-height: 1.5;
    }

    &::after {
        content: '';
        position: absolute;
        width: rem(60);
        height: rem(6);
        background: $main-color;
        opacity: 0.2;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);

        @include mq('md') {
            width: rem(100);
            height: rem(9);
        }
    }
}

.section-title span {
    display: inline-block;
    text-align: center;
    padding-bottom: rem(21);
    font-size: max(rem(30), 28px);
    font-family: $english-font-family;
    font-weight: 700;
    color: $main-color;
    padding-left: rem(20);
    position: relative;
    opacity: 0.5;

    @include mq('md') {
        padding-bottom: rem(30);
        font-size: max(rem(45), 42px);
    }
}


#screening .section-title span {
    @include mq('md') {
        padding-bottom: rem(37);
    }
}

.section-title--s span {
    &::before {
        content: 'S';
        position: absolute;
        font-size: max(rem(65), 60px);
        font-weight: 700;
        opacity: 0.3;
        bottom: rem(24);
        left: rem(-11);

        @include mq('md') {
            bottom: rem(43);
            font-size: 96px;
            font-size: max(rem(96), 90px);
            left: rem(-41);
            bottom: rem(17);
        }
        @include mq('xl') {
            left: rem(-30);
            bottom: rem(45);
        }
    }
}

.section-title--e span {
    &::before {
        content: 'E';
        position: absolute;
        font-size: max(rem(65), 60px);
        font-weight: 700;
        opacity: 0.3;
        bottom: rem(24);
        left: rem(-25);
        @include mq('md') {
            font-size: max(rem(96), 90px);
            left: rem(-45);
            bottom: rem(12);
        }
        @include mq('xl') {
            bottom: rem(41);
            left: rem(-30);
        }
    }
}