@use "global" as *;

//ドロワー
$drawer-width: rem(34);
$drawer-height: rem(3);
$drawer-color: $main-color;

.hamburger {
    cursor: pointer;
    width: rem(34);
    height: 100%;
    position: relative;
    z-index: $layer-drawer;
}

.hamburger button {
    padding: 0;
}

.hamburger span {
    position: relative;
    height: $drawer-height;
    width: $drawer-width;
    display: block;
    transition: transform 0.5s;
    background: $drawer-color;
}

.hamburger span:nth-child(1) {
    top: rem(-4);
}

.hamburger span:nth-child(2) {
    top: rem(2);
}

.hamburger span:nth-child(3) {
    top: rem(10);
}

.hamburger.is-open span:nth-child(1) {
    top: rem(9);
    transform: rotate(45deg);
}

.hamburger.is-open span:nth-child(2) {
    opacity: 0;
    transform: translateY(-50%);
}

.hamburger.is-open span:nth-child(3) {
    top: rem(3);
    transform: rotate(-45deg);
}