@use "global" as *;

.sp-nav {
    display: none;
    width: 100%;
    height: 100vh;
    position: absolute;
    right: 0;
    top: 0;
    background-color: #00000080;
    z-index: $layer-header - 1;
    transition: transform 0.3s ease;
    margin-top: rem(65);
    color: #fff;
}

.sp-nav__item a {
    text-align: center;
    display: block;
    padding: rem(20) 0;
    width: 100%;
    border-bottom: 1px solid #fff;
    background-color: $accent-color;
}

.sp-nav__item--green a{
    background-color: $main-color;
}