@use "global" as *;

.screening {
    position: relative;
    padding-bottom: rem(68);

    &::before {
        content: '';
        position: absolute;
        width: 100%;
        background: url(../images/screening/sp_screening.png)no-repeat top center / cover;
        top: rem(232);
        left: 0;
        right: 0;
        z-index: $layer-background;
        bottom: 0;
    }

    margin-top: rem(91);

    @include mq('lg') {
        margin-top: rem(100);
        &::before {
        background: url(../images/screening/screening.png)no-repeat top center / cover;
            top: rem(50);
            bottom: rem(-100);
        }
    }

    @include mq('lg') {
        margin-top: rem(202);
    }
}

.screening__section-title {
    padding-right: rem(3);
}


.screening__box {
    @include mq('md') {
        display: flex;
        justify-content: space-between;
        margin-top: rem(71);
        width: rem(1140);
        max-width: 100%;
    }
}

.screening__content {
    margin-top: rem(82);

    // @include mq('md') {
    //     margin-left: rem(78);
    // }

    @include mq('md') {
        width: 54%;
        margin-top: 0;
        margin-left: rem(50);
    }

    @include mq('lg') {
        margin-top: rem(50);
        margin-left: rem(70);
    }
    
    @include mq('xl') {
        margin-left: rem(-17);
        margin-top: rem(76);
    }
}

.screening__subtitle {

    @include mq('md') {
        margin-top: 0;
    }
}

.screening__text {
    margin-top: rem(27);
    line-height: 1.875;
    font-size: max(rem(16), 14px);
    text-align: justify;

    @include mq('md') {
        // width: rem(620);
        letter-spacing: 0;
        max-width: 100%;
        padding-right: rem(40);
        font-size: max(rem(18), 16px);
        margin-top: rem(32);
    }

    @include mq('xl') {
        padding-right: rem(83);
    }
}

.screening__img {
    height: auto;

    @include mq('md') {
        border-radius: 20px;
        overflow: hidden;
    }
}

.screening__img img {
    -o-object-fit: contain;
    object-fit: contain;
    height: auto;
    @include mq('md') {
        border-radius: 20px;
    }
}

.screening__img1 {
    padding-top: rem(18);
    margin-left: calc(50% - 50vw);
    height: auto;
    aspect-ratio: 264/ 170;
    margin-right: rem(70);

    // @include mq('md') {
    //     padding-top: rem(30);
    // }

    @include mq('md') {
        border-radius: 10px;
        overflow: hidden;
        margin-right: 0;
        margin-left: rem(0);
        padding-top: rem(0);
        margin-top: rem(149);
        width: rem(540);
    width: 46%;

        max-width: 100%;
        position: relative;
        z-index: $layer-up;
        aspect-ratio: 541 / 356;
    }
}

.screening__img2 {
    padding-top: rem(29);
    margin-right: calc(50% - 50vw);
    height: auto;
    aspect-ratio: 289 / 170;
    margin-left: auto;
    width: 90%;


    // @include mq('md') {
    //     padding-top: rem(30);
    // }

    @include mq('md') {
        border-radius: 10px;
        overflow: hidden;
        width: 56%;
        padding-top: rem(10);
        margin-right: 0;
        margin-inline: auto;
        margin-top: rem(-73);
        aspect-ratio: 693 / 419;
        margin-left: rem(88);
    }

    @include mq('lg') {}
}

.screening__item {
    margin-top: rem(40);

    @include mq('md') {
        margin-top: 100px;
    }

    @include mq('lg') {
        position: relative;
        width: rem(537);
        max-width: 100%;
        margin-inline: auto;
    }

    @include mq('xl') {
        margin-top: rem(207);
    }
}

.screening__head {
    font-weight: 500;
    font-size: max(rem(15), 14px);
    background-color: #D9B983;
    color: #fff;
    border-radius: 0 rem(50) rem(50) 0;
    line-height: 1.4;
    text-align: center;
    padding: rem(5);
    padding-left: rem(12);
    margin-left: calc(50% - 50vw);
    width: 67%;
}

.screening__head--sp {

    @include mq('md') {
        display: none;
    }
}

.screening__head--pc {
    display: none;

    @include mq('md') {
        font-weight: 500;
        font-size: max(rem(15), 14px);
        background-color: #D9B983;
        color: #fff;
        line-height: 1.4;
        display: inline-block;
        position: absolute;
        border-radius: 50%;
        width: 100px;
        height: 100px;
        top: -50px;
        left: -50px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    @include mq('xl') {
        width: rem(120);
        height: rem(120);
        top: rem(-56);
        left: rem(-71);
    }
}

.screening__picture {
    position: relative;
    margin-top: rem(21);
    width: rem(537);
    max-width: 100%;
    margin-inline: auto;
    height: auto;

    @include mq('md') {
        margin-top: rem(30);
        width: 80%;
    }

    @include mq('md') {
        width: rem(803);
        max-width: 100%;
    }

    @include mq('xl') {
        width: 100%;
        margin-top: 0;
        position: relative;
    }
}

.screening__picture img {
    height: auto;
    aspect-ratio: 315 / 541;

    @include mq('md') {
        aspect-ratio: 537 / 926;
    }
}

.screening__below {
    width: rem(537);
    max-width: 100%;
    margin-inline: auto;

    @include mq('md') {}
}

.screening__comment {}

.screening__comment-sp {
    width: rem(234);
    width: 74.2%;
    margin-left: auto;
    position: relative;
    margin-top: rem(19);

    @include mq('lg') {
        display: none;
    }
}

.screening__comment-pc {
    @include mq('lg') {
        width: rem(250);
        // height: 138px;
        margin-left: rem(50);
        top: rem(-635);
        left: 100%;
        position: relative;
    }

    @include mq('xl') {
        top: rem(-736);
        margin-left: rem(19);
    }
}



.screening__btn {
    padding-top: rem(52);
    text-align: center;
    margin-inline: auto;

    @include mq('md') {
        margin-top: rem(30);
        display: flex;
        justify-content: center;
        padding-top: 0;
        // padding-top: rem(139);
        // padding-bottom: rem(177);
    }

    @include mq('xl') {
        margin-top: 0;
    }
}

#screening .common-btn {
    padding: rem(11) rem(16);
    width: rem(278);
    max-width: 100%;
    display: block;
    font-size: max(rem(20), 18px);
    font-weight: 600;
    margin-inline: auto;
    text-align: center;
    padding-left: 0;
    padding-right: 0;

    @include mq('md') {
        letter-spacing: 0.05em;
        width: rem(600);
        max-width: 100%;
        padding: rem(17);
        font-size: max(rem(25), 23px);
        border-radius: rem(50);
        padding-right: rem(20);
        padding-left: rem(12);
    }

    @include mq('lg') {
        font-size: max(rem(30), 28px);
        width: rem(461);

    }
}

.screening__below {
    @include mq('md') {
        position: relative;
        padding-bottom: rem(100);

        &::before {
            content: '';
            position: absolute;
            background: url(../images/screening/sp_gradation_bg.png)no-repeat top center / cover;
            width: 100%;
            height: 100%;
            margin: 0 calc(50% - 50vw);
            width: 100vw;
        }
    }

    @include mq('lg') {
        padding-bottom: 0;

        &::before {
            display: none;
        }
    }

}