@use "global" as *;

.mainview {
    margin-top: rem(63);
    position: relative;
    z-index: $layer-floating + 1;
    font-family: $second-font-family;
    background: url(../images/mv/sp_mv_ph.png)no-repeat center center / cover;
    width: 100%;
    height: rem(300);


    @include mq('md') {
        margin-top: 0;
        background: url(../images/mv/mv_ph.png)no-repeat center center / cover;
        height: rem(820);

    }
}

.mainview__title {
    position: absolute;
    top: rem(3);
    left: 24%;
    // left: 22%;
    transform: translateX(-50%);
    letter-spacing: rem(0.5);
    -webkit-writing-mode: vertical-rl;
    -ms-writing-mode: tb-rl;
    writing-mode: vertical-rl;
    // letter-spacing: 0.1em;
    // padding-bottom: rem(10);
    @include mq('md') {
        letter-spacing: 0.3em;
        top: 20%;
    }
    @include mq('xl') {
        top: 23%;
        left: 25%;

    }
}

.mainview__head {
    padding-left: rem(13);
    font-size: max(rem(32), 30px);
    font-weight: 400;
    line-height: 1.1875;
    position: relative;
    letter-spacing:0.15em;
        &::before {
        content: '';
        position: absolute;
        display: inline-block;
        z-index: $layer-background;
        left: rem(3);
        top: rem(6);
        width: rem(80);
        height: rem(80);
        background-size: contain;
        background: repeating-linear-gradient(-45deg, #B3D2F0, #B3D2F0 8px, transparent 8px, transparent 16px);
        border-radius: 50%;
        @include mq('md') {
            width: rem(207);
            height: rem(207);
            top: rem(-30);
            left: rem(21);
        }
    }
    @include mq('md') {
        font-size: max(rem(63), 61px);
        font-weight: 400;
    }
    @include mq('lg') {
        padding-left: rem(42);

    }
}

.mainview__lead {
    margin-top: rem(40);
    @include mq('md') {
        margin-top: 74px;
    }
    @include mq('lg') {
        margin-top: rem(81);
    }

}

.mainview__text {

    display: block;
    font-size: rem(16);
    font-size: max(rem(16), 14px);
    font-weight: 400;
    letter-spacing:0.17em;

    @include mq('md') {
        font-weight: 400;
        font-size: rem(30);
    }
    @include mq('lg') {

    }
}

.mainview__text:not(:last-child) {
    padding-left: rem(3);
    @include mq('lg') {
        padding-left: rem(21);
    }
}

.mainview__line {
    display: inline-block;
    position: relative;
    padding-top: rem(5);
    padding-bottom: rem(20);
    @include mq('md') {
        padding-bottom: rem(40);
    }
        &::after {
        content: '';
        position: absolute;
        height: rem(20);
        width: rem(1);
        background: $black;
        @include mq('md') {
            height: rem(40);
        }
    }
}

.mainview__img {
    @include mq('md') {
    }
}

