@use "global" as *;

.read {
    background: url(../images/mv/sp_read_bg.png)no-repeat center center / cover;
    width: 100%;
    padding-top: rem(23);
    padding-bottom: rem(37);

    @include mq('lg') {
        background: url(../images/mv/read_bg.png)no-repeat center center / cover;
        padding-top: rem(106);
        padding-bottom: rem(74);
    }
}

.read__inner {
    @include mq('md') {
        max-width: 600px;
    }

    @include mq('lg') {
        max-width: 800px;
    }

    @include mq('xl') {
        display: flex;
        margin-inline: auto;
        max-width: rem(1484);
    }
}

.read__subtitle {
    font-size: max(rem(24), 20px);
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: 0.05em;
    font-family: $second-font-family;

    @include mq('md') {
        font-size: max(rem(30), 26px);
        max-width: 100%;
        margin-inline: auto;
    }

    @include mq('lg') {
        font-size: max(rem(36), 28px);
        line-height: 1.75;
        margin-left: 0;
        margin-right: 0;
    }

    @include mq('xl') {
        width: 35.913528%;
    }
}

.read__subtitle p:not(:first-child) {
    padding-left: rem(90);

    @include mq('xl') {
        padding-left: rem(148);
    }
}

.read__content {
    padding-top: rem(11);
    line-height: 1.8125;
    font-size: max(rem(16), 14px);
    letter-spacing: 0.036em;

    @include mq('md') {
        line-height: 1.9;
        font-size: max(rem(18), 16px);
        letter-spacing: 0.01em;
        margin-inline: auto;
        margin-top: rem(20);
    }

    @include mq('xl') {
        font-size: max(rem(20), 18px);
        width: rem(660);
        max-width: 100%;
        margin-top: rem(-5);
        padding-top: 0;
        margin-left: rem(20);
    }
}