@use "global" as *;

.footer {
    text-align: center;
    padding-top: rem(29);
    font-size: max(rem(16),14px);
    @include mq('md') {
        padding-top: rem(45);
    }
}

#footer .container {
    padding-right: rem(25);
    padding-left: rem(25);
}

.footer__body {
    @include mq('md') {
        padding-left: rem(21);

    }

}

.footer__link {
    text-decoration: underline;
    padding: rem(3);
    letter-spacing: 0;
    @include mq('md') {
        // padding-left: rem(21);
    }

}

.footer__logo {
    width: rem(284);
    padding: rem(3);
    margin-inline: auto;
    padding-top: rem(10);
    @include mq('md') {
        width: rem(316);
        margin-top: rem(20);
    }

}

.logo {

}

.footer__company {
    padding-top: rem(20);
    @include mq('md') {
        padding-top: rem(34);
    }
}

.footer__company p:nth-child(2){
    padding-top: rem(15);
}

.footer__company p:nth-child(3){
    padding-top: rem(8);
    @include mq('lg') {
        padding-top: rem(14);
    }
}

.footer__text {
    padding-top: rem(17);
    line-height: 1.625;
    @include mq('lg') {
        padding-top: rem(36);
    }
}

.footer__text br{
    @include mq('lg') {
        display: none;
    }
}

.footer__copyright {
    margin-top: rem(31);
    background: $main-color;
    color: #fff;
    padding-top: rem(16);
    padding-bottom: rem(18);
    font-size: max(rem(16), 14px);
    letter-spacing: 0;
    @include mq('md') {
        padding: rem(10);
        padding-bottom: rem(23);
        margin-top: rem(43);
        padding-top: rem(18);
    }
}

