@use "global" as *;

//header

.header {
    background-color: #ffffff80;
    height: rem(65);
    position: fixed;
    width: 100%;
    z-index: $layer-header;
    top: 0;
    left: 0;
    right: 0;
    font-weight: 600;
    transition: background-color 1s;
    @include mq('md') {
        height: rem(100);
    }
}

.header__inner {
    padding: 0 rem(30);
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: inherit;
    padding-right: rem(36);

    @include mq('md') {
        padding-right: rem(50);
        padding-left: rem(43);
    }
}

#header .container{
        padding-right: rem(36);
}

.header__logo{
    width: rem(200);
    @include mq('md') {
        width: rem(300);
    }
}
