@use "global" as *;

.contact {
    padding-top: rem(35);
    padding-bottom: rem(40);
    text-align: center;
    background-color: #FAEEEC;

    @include mq('lg') {
        padding-top: rem(66);
        padding-bottom: rem(82);
        margin-top: rem(162);
    }
}

#contact .container {
    padding-left: rem(25);
    padding-right: rem(25);
}

.contact p {
    font-size: max(rem(22), 20px);
    font-weight: 700;
    line-height: 1.6818181818;
    letter-spacing: 0.03em;

    @include mq('lg') {
        font-size: max(rem(30), 28px);
    }
}

.contact__inner br {
    @include mq('lg') {
        display: none;
    }
}

.contact__btn {
    margin-top: rem(29);
    text-align: center;

    @include mq('lg') {
        display: flex;
        justify-content: center;
        margin-top: rem(45);
    }
}

// #contact 
#contact .common-btn {
    display: block;
    margin-inline: auto;
    width: rem(195);
    max-width: 100%;
    text-align: center;
    font-size: max(rem(20), 18px);
    font-weight: 600;
    padding: rem(13) rem(16);

    @include mq('md') {
        border-radius: rem(50);
        letter-spacing: 0.05em;
        width: 200px;
        width: rem(600);
        font-size: max(rem(25), 23px);
        max-width: 100%;
    }

    @include mq('lg') {
        width: rem(270);
        font-size: max(rem(30), 28px);
        font-weight: 600;
        margin-left: rem(21);
        margin-right: 0;
        padding: rem(19) rem(16);
    }
}

#contact .common-btn:not(:first-child) {
    margin-top: rem(21);

    @include mq('lg') {
        margin-top: rem(0);
        margin-left: rem(41);
    }
}

