@use "global" as *;

/*==================================================================
# PC
================================================================== */

.header__pc-nav {
    height: inherit;
}

.pc-nav__items {
    display: flex;
    align-items: center;
    height: inherit;
    justify-content: end;
}


.pc-nav__item a {
    display: inline-block;
    font-size: max(rem(18),16px );
    font-weight: 600;
}

.pc-nav__item:not(:first-child) {
    margin-left: rem(22);
}

.pc-nav__item .common-btn--test {
    @include mq('md') {
        
    }
    @include mq('lg') {
        width: rem(270);
    }
}

.pc-nav__item .common-btn--red,
.pc-nav__item .common-btn--green {
    @include mq('md') {
        
    }
    @include mq('lg') {
        width: rem(160);
    }
}

