@use "global" as *;

.effect {
    margin-top: 88px;
    width: 660px;
    max-width: 100%;
    margin-inline: auto;

    @include mq('lg') {
        width: 100%;
        margin-top: rem(291);
    }
}

.sffect__container {

}

#effect .container {
    padding-right: rem(25);

}

.section-title {

}

#effect .section-title span {
    @include mq('lg') {
        padding-left: rem(47);
        padding-bottom: rem(30);
    }
}



#effect .section-title h2 {
    margin-left: rem(-9);
    margin-top: rem(6);
    @include mq('lg') {
        margin-top: 15px;
        padding-bottom: 61px;
    }

}

.effect__text {
    margin-top: rem(53);
    line-height: 1.875;
    font-size: max(rem(16),14px);
    letter-spacing: 0.05em;
    @include mq('lg') {
        margin-top: rem(130);
        width: rem(1038);
        max-width: 100%;
        margin-inline: auto;
        line-height: 1.88888;
        font-size: 18px;
        letter-spacing: 0;
    }
}

.effect__box {
    background: repeating-linear-gradient(-45deg, white, white 5px, #F7E8E4 5px, #F7E8E4 10px);
    border-radius: rem(30);
    margin-top: rem(30);
    font-weight: 500;
    padding-top: rem(35);
    padding-bottom: rem(35);
    @include mq('md') {
        margin-top: rem(50);
        padding-top: rem(56);
        padding-bottom: rem(60);
    }
    @include mq('lg') {
        display: flex;
        align-items: center;
        justify-content: center;
        width: rem(1225);
        max-width: 100%;
        margin-inline: auto;
        margin-top: rem(106);
    }

}

.effect__head {
    font-size: max(rem(22),20px );
    font-weight: 500;
    text-align: center;
    line-height: 1.8181818181;
    letter-spacing: 0.05em;
    @include mq('md') {
        font-size: 24px;
    }
    @include mq('lg') {
        font-size: 26px;
        line-height: 1.73076923;
        width: rem(315);
        max-width: 100%;
        margin-inline: auto;
        // margin-top: rem(60);
        margin-left: rem(97);
        letter-spacing: 0.1em;
    }

}

.effect__lists {
    font-size: max(rem(18),16px );
    line-height: 1.666666;
    letter-spacing: 0.05em;
    width: rem(320);
    max-width: 100%;
    margin-inline: auto;
    padding-left: rem(22);
    @include mq('md') {
        width: 45%;
        
    }
    @include mq('lg') {
        font-size: rem(22);
        width: rem(700);
        margin-left: 0;
        letter-spacing: 0.1em;
        padding-left: rem(15);
    }
}

.effect__list {
    margin-top: rem(27);
    display: flex;
    align-items: center;
    @include mq('lg') {
        margin-top: 0;
    }
        &::before {
        content: '';
        position: absolute;
        background-color: $accent-color;
        width: rem(43);
        height: rem(43);
        border-radius: 50%;
        left: rem(-58);
        top: 50%;
        transform: translateY(-50%);
        color: #fff;
        font-size: rem(24);
        // font-size: rem(35);
        font-weight: 500;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: $second-font-family;
    }
}

.effect__number {
    background-color: $accent-color;
    width: 43px;
    height: 43px;
    border-radius: 50%;
    color: #fff;
    font-size: 24px;
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;
    // font-family: $second-font-family;
    @include mq('lg') {
        width: 52px;
        height: 52px;
    }
}

.effect__item {
    margin-left: rem(15);
    @include mq('md') {
        margin-left: rem(31);
    }
}

.effect__item br {
    @include mq('lg') {
        display: none;
    }
}

.effect__number--serif {
    // font-family: serif;
    // font-weight: bold;
}

.effect__list:nth-of-type(2) {
    margin-top: rem(21);
    @include mq('lg') {
        margin-top: 38px;
    }
}

.effect__list:nth-of-type(3) {
    margin-top: rem(20);
    @include mq('lg'){
        margin-top: 34px;
    }
}

#effect .sub-title {
    @include mq('lg') {
        font-size: 30px;
        // margin-left: 66px;
        -moz-column-gap: rem(30);
             column-gap: rem(30);
             margin-left: rem(10);
    }
}