@use "global" as *;

.sub-title {
    font-family: $second-font-family;
    font-weight: 500;
    font-size: max(rem(22),20px);
    // margin-left: rem(42);
    line-height: 1.5909090909;
    letter-spacing: 0.1em;
    display: flex;
    -moz-column-gap: rem(10);
         column-gap: rem(10);
        &::before {
        content: '';
        display: inline-block;
        background-image: url(../images/screening/sp_icon.png);
        background-position: center center;
        background-repeat: no-repeat;
        background-size: contain;
        width: rem(32);
        height: rem(32);
        // left: rem(-40);
        @include mq('md') {
            width: rem(47);
            height: rem(47);
            // left: rem(-62);
        }
    }

    @include mq('xl') {
        font-size: 30px;
        margin-left: 0;
    }
}

#screening .sub-title {
    // margin-left: rem(41);
    @include mq('md') {
        margin-left: rem(-67);
    }
}

#screening .sub-title span {
    padding-left: rem(8);
    @include mq('md') {
        padding-left: 0;
        margin-top: rem(-8);
    }
}

#effect .sub-title {
    &::before {
        display: none;
        @include mq('md') {
            display: block;
        }
    }

}